
<script>

export default {
  name: 'VueDatePicker',
  props: {
    //true:UI;false: Viy PC Ui
    showMobileUi:{
      type:Boolean,
      default:true
    }
  },
  data() {
    return {
    };
  },
  render(h) {
    const slots = this.$slots?Object.keys(this.$slots)
            .reduce((arr, key) => arr.concat(this.$slots[key]), [])
              .map(vnode => {
                vnode.context = this._self
                return vnode
            }):null; 
     return h(this.showMobileUi?'VueDatePickerMobile':'VueDatePickerViy',{
      attrs:this.$attrs,
      scopedSlots: this.$scopedSlots,
      on:this.$listeners
    }, slots); 



  },
  methods: {
  },
  watch: {
  },
  computed: {
  },
  
};
</script>
